import { useState, useEffect } from 'react';
import './randomChar.scss';
//import thor from '../../resources/img/thor.jpeg';
import mjolnir from '../../resources/img/mjolnir.png';
import useMyMarvelService from '../../services/MyMarvelService';
import Spinner from '../spinner/Spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';

const RandomChar =() => {
    const [char, setChar] = useState(null);
    
    /* state = {
        char: {},
        loading: true,
        error: false
    } */

    const {loading, error, getCharacter, clearError } = useMyMarvelService();
     
    useEffect(() => {
        updateChar();
        const timerId = setInterval(updateChar, 60000);

        return () => {
            clearInterval(timerId)
        }
    }, [])

    /* componentDidMount() {
        this.updateChar();
        //this.timerId = setInterval(this.updateChar, 10000);
    }   
    
    componentWillUnmount() {
        clearInterval(this.timerId);
    } */

    const onCharLoaded = (char) => {
        setChar(char);
        
        
        /* this.setState({
            char,
            loading: false
        }) */
    }


    const updateChar = () => {
        clearError();
        const id = Math.floor(Math.random() * 20) + 1;
        getCharacter(id)
            .then(onCharLoaded);       
    }

        //const {char, loading, error} = this.state;
    const errorMessage = error ? <ErrorMessage/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(loading || error || !char) ? <View char={char}/> : null;

    return (
        <div className="randomchar">
            {errorMessage}
            {spinner}
            {content}
            <div className="randomchar__static">
                <p className="randomchar__title">
                    Random character for today!<br/>
                    Do you want to get to know him better?
                </p>
                <p className="randomchar__title">
                    Or choose another one
                </p>
                <button className="button button__main"
                        onClick={updateChar}
                        >
                    <div className="inner">try it</div>
                </button>
                <img src={mjolnir} alt="mjolnir" className="randomchar__decoration"/>
            </div>
        </div>
    )  
}

const View = ({char}) => {
    const {name, description, thumbnail, homepage, wiki} = char;
    const isImageNotAvailable = thumbnail.includes('image_not_available');
    const imgStyle = isImageNotAvailable ? { objectFit: 'contain' } : { objectFit: 'cover' };

    return (
        <div className="randomchar__block">
            <img src={thumbnail} 
                 alt="Random character" 
                 className="randomchar__img"
                 style={imgStyle}/>
            <div className="randomchar__info">
                <p className="randomchar__name">{name}</p>
                <p className="randomchar__descr">
                    {description}
                </p>
                <div className="randomchar__btns">
                    <a href={homepage} className="button button__main">
                        <div className="inner">homepage</div>
                    </a>
                    <a href={wiki} className="button button__secondary">
                        <div className="inner">Wiki</div>
                    </a>
                </div>
            </div>
        </div>
    )
}

export default RandomChar;